import React, {useState} from 'react'
import { useStaticQuery, graphql, Link  } from 'gatsby'
import Pagination from '../../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import PropertyItemV2 from './PropertyItem.component'
import { SearchResult } from '../../../../../Global/Pages/Listing/Grid/GridSearchResult.component'
const V2Section=()=>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
            fp21: file(relativePath: {eq: "property/fp21.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            
            fp22: file(relativePath: {eq: "property/fp22.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp23: file(relativePath: {eq: "property/fp23.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp24: file(relativePath: {eq: "property/fp24.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp25: file(relativePath: {eq: "property/fp25.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp26: file(relativePath: {eq: "property/fp26.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			
        }`)
    return(
        <section className="our-listing bgc-f7 pb30-991">
			<div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar index="1" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-6">
                        <div className="breadcrumb_content style2 mt30-767 mb30-767">
                            <ol className="breadcrumb">
                                <li className="breadcrumb-item"><Link to="/">Home</Link></li>
                                <li className="breadcrumb-item active text-thm" aria-current="page">Simple Listing – Grid View</li>
                            </ol>
                            <h2 className="breadcrumb_title">Simple Listing – Grid View 2</h2>
                        </div>
                    <div className="dn db-991">
                    <div id="main2" >
                        <span id="open2" className="flaticon-filter-results-button filter_open_btn style3 mt20" onClick={handleSidebar}> Show Filter</span>
                    </div>
                </div>
                </div>
                
                </div>
                <div className="row">
                    <div className="col-lg-4 col-xl-4">
                    <div className="sidebar_listing_grid1 dn-991">
						<div className="sidebar_listing_list">
							<div className="sidebar_advanced_search_widget">
                                <AdvanceSearchWidgetSidebar index="2"/>
                            </div>
                        </div>
                    </div>
                    </div>
                    <div className="col-md-12 col-lg-8">
                        <div className="row">
                         <SearchResult/>
                        </div>
                        <div className="row">
                            <div className="col-md-6 col-lg-6">
                               
                               <PropertyItemV2
                                price="13000"
                                title="Renovated Apartment"
                               >
                                   <img className="img-fluid w100" src={data.fp21.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-md-6 col-lg-6">
                           <PropertyItemV2
                                price="48000"
                                title="Gorgeous Villa Bay View"
                               >
                                   <img className="img-fluid w100" src={data.fp22.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItemV2
                                price="13000"
                                title="Renovated Apartment"
                               >
                                   <img className="img-fluid w100" src={data.fp23.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-md-6 col-lg-6">
                           <PropertyItemV2
                                price="48000"
                                title="Gorgeous Villa Bay View"
                               >
                                   <img className="img-fluid w100" src={data.fp24.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItemV2
                                price="13000"
                                title="Renovated Apartment"
                               >
                                   <img className="img-fluid w100" src={data.fp25.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItemV2
                                price="48000"
                                title="Gorgeous Villa Bay View"
                               >
                                   <img className="img-fluid w100" src={data.fp26.childImageSharp.fluid.originalImg} alt="fp21.jpg"/>
                               </PropertyItemV2>
                            </div>
                            <div className="col-lg-12 mt20">
                                <Pagination/>
                            </div>                          
                        </div>
                    </div>
                </div>
            </div>
	    </section>
    )
}
export default V2Section